import React, {useEffect, useRef, useState} from 'react';
import styled from 'styled-components';
import SmallRoutPanel from "./RoutePanel/SmallRoutPanel";
import MBookViewPanel from "./BookVeiwPanel/MBVPanel";
import SyosetuSearchPanel from "./BookVeiwPanel/syosetu/syosetuSearch";

const MCDiv = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  //opacity: 0;
`
const MCPanel = styled.div`
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90%;
  background: #ffffff20;
  border-radius: 0.5rem;
`
const CDiv = styled.div`
  position: absolute;
  height: ${props => props.layout.h || '60%'};
  width: ${props => props.layout.w || '60%'};
  left: ${props => props.layout.x+'%' || 'auto'};
  top: ${props => props.layout.y+'%' || 'auto'};
  ${props => props.layout.float? `float: ${props.layout.float};`  : ''}
  box-sizing: border-box;
  padding: 0.5rem 0.5rem 0.5rem 0.5rem;
`
const CPanel = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
  border-radius: 0.5rem;
  backdrop-filter: blur(10px);
  background: rgba(224, 213, 213, 0.63);
`
const EmptyDiv = styled.div``;

function CDivPanel({layout, Child, content, cdRef, index}) {


    return(
        <CDiv layout={layout}>
            <CPanel>
                <Child {...content}/>
            </CPanel>
        </CDiv>
    )
}


