import React, {useEffect} from 'react';
import styled from 'styled-components';


const RMIButton = styled.button`
  position: absolute;
  //left: 15%;
  width: 70%;
  aspect-ratio: 1/1;
  background-color: #777777;
  border-radius: 50%;
  border: transparent;
  margin: 0 15%;
  transition: top ${props => props.trans? 0 : 0.5}s;
`
const RMFDiv = styled.div`
  position: relative;
  top: 10%;
  height: ${props => props.styleProp?.height || '20rem'};
  width: 91%;
`
const MFPanel = styled.div`
  position: absolute;
  width: 100%;
  height: 90%;
  border-radius: 5%;
  text-align: center;
  background: #777777;
`

function RMIcon({content, FDivRef, topPxUpd, topPx}) {

    function mainIconOnClick() {
        FDivRef.current.scrollIntoView({behavior: "smooth", block: "center"});
    }

    return (
        <RMIButton style={{top: topPx+'px'}}
                   onClick={() => mainIconOnClick()}
                   trans={topPxUpd}
        >
            {content?.text}
        </RMIButton>
    )
}

function RMFPanel({content, FDivRef, topPxUpd, sTopPx, pxPos}) {
    useEffect(() => {
        if (topPxUpd === null || topPxUpd) {sTopPx(FDivRef.current.offsetTop-pxPos)}

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pxPos, topPxUpd])


    return (
        <RMFDiv ref={FDivRef} styleProp={content?.style}>
            <MFPanel>
                {content?.text}
            </MFPanel>
        </RMFDiv>
    )
}

export {RMIcon, RMFPanel}