import React, {useEffect, useRef, useState} from 'react';
import styled from 'styled-components';
// import Observer from "../other/Observer";
import {RMFPanel, RMIcon} from "./RMContentPanel/RMPanelContent";
import {useStateAsyc} from "../other/curHooks";


const RMDiv = styled.div`
  position: absolute;
  right: ${props => props.exp || '-83%'};
  width: 100%;
  top: 50%;
  height: 90%;
  transform: translateY(-50%);
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  box-shadow: 0 0 1rem 0.2rem #00000080;
  //transition: width 1.5s;
  background: #ffffff80;
  backdrop-filter: blur(8px);
  transition: right 1s cubic-bezier(.25,1.18,.36,1.1);
  pointer-events: auto;
`
const RMContentPanel = styled.div`
  position: absolute;
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row;

  overflow: scroll;
  mask-image: linear-gradient(to bottom, transparent 2%, black 5%, black 95%, transparent 98%);

  ::-webkit-scrollbar {
    display: none;
  }
`
const RMFixIconDiv = styled.div`
  position: fixed;
  left: 0;
  width: 4rem;
  height: 100%;
  background: #ffffff20;
`
const RMIDiv = styled.div`
  width: 100%;
  height: 100%;
`
const RMFDiv = styled.div`
  position: absolute;
  left: 4rem;
  flex: 1 1 auto;
  width: ${props => props.wid}px;
  min-height: 100%;
  padding: 10% 0;
  background: #ffffff20;
`

function RMainFuncPanel() {
    const mainDivRef = useRef()
    const funcDivRef = useRef()
    const iconDivRef = useRef()
    const [pxPos, sPxPos] = useState(0)
    const [topPxUpd, sTopPxUpd] = useState(true)
    const [divExpand, sDivExpand] = useState('-83%')
    const [funcDivWidth, sFuncDivWidth] = useState(0)
    const FMPIcons = {}
    const FMPDivs = {}
    // const spareObj = {}

    for (let i = 1; i < 5; i++) {
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const [topPx, sTopPx] = useState(0)
        // eslint-disable-next-line react-hooks/rules-of-hooks
        const FDivRef = useRef()
        const FMPStyle = {}
        FMPIcons['p'+i] = {content: i, FDivRef, topPxUpd, topPx}
        FMPDivs['p'+i] = {content: i, FDivRef, topPxUpd, sTopPx, pxPos, FMPStyle}
    }

    function foldIcon() {
        let curHeight = FMPDivs['p1'].FDivRef.current.offsetTop;
        let nextH = iconDivRef.current.offsetWidth;

        Object.entries(FMPDivs).forEach(([k, v]) => {
            v.sTopPx(curHeight)
            curHeight += nextH
        })
        sTopPxUpd(false);
    }


    function rerenderIcon() {
        sPxPos(mainDivRef.current.scrollTop);
        sTopPxUpd(null);
        setTimeout(function(){sTopPxUpd(true)}, 10)
    }

    function funcDivOnScroll(e) {
        sPxPos(e.target.scrollTop)
    }

    useEffect(() => {
        let mfw = mainDivRef.current.offsetWidth - iconDivRef.current.offsetWidth
        sFuncDivWidth(mfw)
        // sDivExpand('20%')
        foldIcon()

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    // useEffect(()=>{
    //     console.log(topPxUpd)
    // }, [topPxUpd])



    return (
        <RMDiv exp={divExpand} onMouseLeave={() => {
            sDivExpand(-0.97*funcDivWidth+'px')
            setTimeout(foldIcon, 20)
        }}>
            <RMContentPanel ref={mainDivRef} onScroll={(e) => funcDivOnScroll(e)}>
                <RMFixIconDiv ref={iconDivRef} onClick={() => sDivExpand('-5%')}>
                    <RMIDiv onMouseEnter={() => foldIcon()}>
                        {Object.entries(FMPIcons).map(([k, v]) => <RMIcon key={k} {...v}/>)}
                    </RMIDiv>
                </RMFixIconDiv>
                <RMFDiv ref={funcDivRef}
                        wid={funcDivWidth}
                        onMouseEnter={() => {
                            rerenderIcon()
                            sDivExpand('-5%')
                        }}>
                    {Object.entries(FMPDivs).map(([k, v]) => <RMFPanel key={k} {...v}/>)}
                </RMFDiv>
            </RMContentPanel>
        </RMDiv>
    )
}

export default RMainFuncPanel